<template>
  <div class="container pb-3" ref="recad-container">
    <!-- indicador -->
    <div class="indicadores">
      <router-link to="/recadpj/form">1</router-link>
      <div class="divisor"></div>
      <router-link to="/recadpj/form/2">2</router-link>
      <div class="divisor"></div>
      <router-link to="/recadpj/form/3">3</router-link>
      <div class="divisor"></div>
      <router-link to="/recadpj/form/4">4</router-link>
    </div>
    <!-- view dos formularios -->
    <transition mode="out-in" name="fade" @after-leave="$jquery($refs['recad-container']).scrollTop()">
      <router-view :empresa="empresa" v-model="dados" @recadastrar="confirmarDados"/>
    </transition>
    <!-- debug -->
<!--    <h2>Dados do cache:</h2>-->
<!--    <small style="white-space: pre-wrap">{{dados}}</small>-->
<!--    <h2>Dados da Storage:</h2>-->
<!--    <small style="white-space: pre-wrap">{{empresa}}</small>-->
  </div>
</template>

<script>
  import CreciHelper from "../../helpers/CreciHelper";
  export default {
    name: "RecadPj",
    data: () => ({
      dados: {}
    }),
    methods: {
      async confirmarDados() {
        // eslint-disable-next-line no-console
        console.log(JSON.stringify(this.dados)); //TODO - Apenas para debug, apagar depois.
        try {
          await this.submit();
        } finally {
          this.$router.push('/');
        }
      },
      async submit() {
        //Prepara informações para envio
        const dados_gravarempresa = {
          "Token": CreciHelper.getCofeciToken(this.$store.getters.regional),
          "CPFCNPJ": this.empresa.cnpj,
          "CodigoInscricao": this.empresa.creci,
          "IdTipoInscricao": this.empresa.secundaria ? 2 : 1,
          "IdCategoriaInscricao": 1, //this.empresa.categoria,
          "Nome": this.dados.nome.toUpperCase(),
          "NomeFantasia": this.dados.fantasia.toUpperCase(),
          "DataInscricao": this.empresa.data_inscricao,
          "IdTipoEmpresa": this.converteTipoEmpresa(this.dados.tipo),
          "MatrizFilial": this.dados.matriz === 1 ? 'M' : 'F',
          "InscricaoJuntaComercial": this.dados.junta,
          "DataInscricaoJuntaComercial": this.dados.juntaData,
          "NumeroAlteracaoContratual": parseInt(this.dados.altContrato),
          "DataAlteracaoContratual": this.dados.altContratoData,
          "ValorCapitalSocial": this.dados.capital,
          "Situacao": (this.empresa.situacao === 1) ? "A" : "I",
          "DebitoCreci": this.empresa.debito ? 'S' : 'N',
          "Processo": "N" //Tratativa temporária. Adquirir essa informação.

        };
        const dados_resptecnico = {
          "Token": CreciHelper.getCofeciToken(this.$store.getters.regional),
          "CodigoInscricao":  this.empresa.creci,
          "CodigoInscricaoR": this.$store.state.creci,
          "IdCategoriaInscricao": 1,  //this.empresa.categoria,
          "IdCategoriaInscricaoR": 1, //this.$store.state.categoria,
          "ValorQuota": 0, //(this.empresa.socios.length === 0) ? 100 : (dados_sociedade ? dados_sociedade.cota : 0),
          "IdAtividade": 1, //dados_sociedade && dados_sociedade.cota !== 100 ? 2 : 1,
          "AtivoInativo": "A"
        };
        const demais_socios = this.dados.socios
          .map(socio => ({
            "Token": CreciHelper.getCofeciToken(this.$store.getters.regional),
            "CodigoInscricao": this.empresa.creci,
            "IdCategoriaInscricao": 1, //this.empresa.categoria,
            "CPFCNPJ": socio.cpf,
            "Nome": socio.nome,
            "ValorQuota": socio.cota,
            "AtivoInativo": "A"
          }));

        if (this.$store.state.homologacao) {
          dados_gravarempresa.homologacao = true;
          dados_resptecnico.homologacao = true;
        }

        //Dados para o histórico no banco interno do App
        let histForm = new FormData();
        histForm.append("gravarempresa", JSON.stringify(dados_gravarempresa));
        histForm.append("gravarrt", JSON.stringify(dados_resptecnico));
        histForm.append("gravarsocios", JSON.stringify(demais_socios));

        //Realiza o envio das informações
        try {
          //=== COFECI ===//
          this.$store.commit('setLoading', 'Registrando no COFECI');
          try {
            //Gravar Empresa
            await this.$http.cofeciApi.post('/gravarempresa.php', dados_gravarempresa);
            //Gravar Responsável Técnico
            await this.$http.cofeciApi.post('/gravarresponsaveltecnico.php', dados_resptecnico);
            //Gravar Sócios
            if (demais_socios.length) {
              for (const socio of demais_socios) {
                if (this.$store.state.homologacao) socio.homologacao = true;
                await this.$http.cofeciApi.post('/gravarsocio.php', socio);
              }
            }
          } catch (e) {
            histForm.append("falha", JSON.stringify(e));
            if (e && e.data && e.data.Mensagem) this.$toastr.warning(e.data.Mensagem, 'Aviso do COFECI');
            else this.$toastr.error('Falha ao registrar no COFECI');
            throw new Error('Erro na API COFECI');
          }

          //=== CRECI ===//
          if (!this.$store.state.homologacao) {
            this.$store.commit('setLoading', 'Registrando no CRECI');
            try {
              //
            } catch (e) {
              histForm.append("falha", JSON.stringify(e));
              this.$toastr.error('Falha ao registrar no CRECI');
              throw new Error('Erro na API CRECI');
            }
          }
        } finally {
          this.$store.commit('setLoading', false);
          this.$http.axios.post(this.$http.appApi.defaults.baseURL + '/recad/registrar_pj.php', histForm, {
            headers: {'Authorization': this.$store.state.token}
          });
        }
      },
      converteTipoEmpresa(categoria) {
        //Converte o número-tipo da empresa do ConselhoNET pra o número-tipo do COFECI
        switch (categoria) {
          case 6:  //Firma individual (FI)
            return 4;
          case 7:  //Sociedade Anonima (SA)
            return 5;
          case 8:  //Sociedade Limitada (LTDA)
            return 1;
          case 16: //Eireli
            return 2;
          default:
            return null;
        }
      }
    },
    computed: {
      empresa() {return this.$route.params.empresa ? this.$route.params.empresa : null}
    },
    created() {
      if (!this.empresa) {
        this.$toastr.info("Selecione a empresa");
        this.$router.push({name: 'recadpj-empresas'});
      }
    }
    // created() {
    //   // this.empresas = this.$store.state.pj;
    // },
    // methods: {
    //   dataValidator(data) {
    //     if (!data) return false;
    //     if (data.length !== 10) return false;
    //     if (parseInt(data.substr(6,4)) < 1900 || parseInt(data.substr(6,4)) > (new Date()).getFullYear()) return false;
    //     if (parseInt(data.substr(3,2)) > 12) return false;
    //     if (parseInt(data.substr(0,2)) > 31) return false;
    //     return true;
    //   },
    //   converteTipoEmpresa(categoria) {
    //     //Converte o número-tipo da empresa do ConselhoNET pra o número-tipo do COFECI
    //     switch (categoria) {
    //       case 6:  //Firma individual (FI)
    //         return 4;
    //       case 7:  //Sociedade Anonima (SA)
    //         return 5;
    //       case 8:  //Sociedade Limitada (LTDA)
    //         return 1;
    //       case 16: //Eireli
    //         return 2;
    //       default:
    //         return null;
    //     }
    //   }
    // },
  }
</script>

<style scoped>
  .indicadores{
    display: flex;
    justify-content: space-between;
    pointer-events: none !important;
    padding: 0 .25rem;
  }
  .indicadores a{
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    box-shadow: 0 0 3px rgba(0,0,0,.7);
    position: relative;
    font-weight: 600;
    font-size: 1.1rem;
    font-family: serif;
    text-decoration: none;
    color: black;
    background: #ffffff;
    margin: 1rem 0;
  }
  .indicadores a::after{
    background-color: rgba(0,0,0,0);
    content: "";
    opacity: 0.15;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;
    position: absolute;
    margin: 0.25rem;
  }
  .indicadores a.router-link-exact-active{
    background-color: #28a745;
    color: #ffffff;
    box-shadow: inset 0 0 0.3rem black;
  }
  .divisor{
    padding: 0 .5rem;
    display: flex;
    align-items: center;
    flex: 1 1 auto;
  }
  .divisor::after{
    content: "";
    width: 100%;
    border-bottom: 1px solid rgba(0,0,0,.3);
  }
</style>
